label {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background: #ebebeb;
  border-radius: 10px;
  display: block;
  position: relative;
  box-shadow: inset 0 5px 15px rgba(0, 0, 0, .4), inset 0 -5px 15px rgba(255, 255, 255, .4);
}

label:after {
  content: "";
  width: 13px;
  height: 13px;
  background: linear-gradient(#ffcc89, #d8860b);
  border-radius: 8px;
  position: absolute;
  top: 4px;
  left: 4px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}

input.checkbox {
  width: 0;
  height: 0;
  visibility: hidden;
}

input:checked + label {
  background: #242424;
}

input.checkbox:checked + label:after {
  background: linear-gradient(#777, #3a3a3a);
  left: 37px;
  transform: translateX(-100%);
}

label, label:after {
  transition: all .3s;
}

label:active:after {
  width: 20px;
}

label svg {
  width: 12px;
  height: 12px;
  z-index: 100;
  position: absolute;
  top: 4px;
}

label svg.sun {
  fill: #fff;
  transition: all .3s;
  left: 4px;
}

label svg.moon {
  fill: #7e7e7e;
  transition: all .3s;
  left: 24px;
}

input:checked + label svg.sun {
  fill: #7e7e7e;
}

input:checked + label svg.moon {
  fill: #fff;
}

